import React from 'react';
import { Card, Text, CardBody, Spinner, CardHeader } from '@chakra-ui/react';
import Form from './Form';

const CardWeather = function ({weather, setCity, fetchWeather}) {
    return (  
        <div className='weather'>
            <Form setCity={setCity} fetchWeather={fetchWeather}></Form>
            {weather ? (
                 <Card className='card'>
                    <CardHeader className='cardHead'>
                        <Text>{weather.name}</Text>
                    </CardHeader>
                    <CardBody className='cardBody'>
                        <div className='cardLeft'>
                            {
                                weather.weather[0].main == 'Rain' ? (<img src='data:image/png;base64,' alt='rain'/>) : 
                                weather.weather[0].main == 'Clouds' ? (<img src='data:image/png;base64,' alt='clouds'/>) :
                                weather.weather[0].main == 'Clear' ? (<img src='data:image/png;base64,' alt='clear'/>) :
                                weather.weather[0].main == 'Mist' ? (<img src='data:image/png;base64,' alt='mist'/>) :
                                (<div></div>)
                            }
                            <Text>{weather.main.temp}C</Text>
                        </div>
                        <div className='cardRight'>
                            <Text>: {weather.main.humidity}%</Text>
                            <Text> : {weather.wind.speed} m/s</Text>
                            <Text>: {weather.weather[0].description}</Text>
                        </div>
                    </CardBody>
               </Card>
            ) 
            : 
            (
                <Spinner
                    thickness='4px'
                    speed='0.65s'
                    emptyColor='gray.200'
                    color='blue.500'
                    size='xl'
                />
            )
            }
        </div>
    );
}

export default CardWeather;